import styles from "../common/Auth.module.scss";
import { ReactComponent as Logo } from "../assets/image/ac-logo.svg";
import { Link, useNavigate } from "react-router-dom";
import { useState } from "react";
import { signup } from "../api/auth";
import TimePopup from "../components/TimePopup/TimePopup";
import clsx from "clsx";

const SignUpPage = () => {
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  // 
  const [notiStatus, setNotiStatus] = useState("finished");
  // 
  const [accountInvalid, setAccountInvalid] = useState(false);
  const [emailInvalid, setEmailInvalid] = useState(false);

  const navigate = useNavigate();

  const handleClick = async () => {
    if (
      account.length === 0 ||
      name.length === 0 ||
      email.length === 0 ||
      password.length === 0 ||
      checkPassword === 0
    ) {
      setNotiStatus("incomplete");
      return;
    }
    if (password !== checkPassword) {
      setNotiStatus("code");
      return;
    }
    const data = await signup({
      account,
      name,
      email,
      password,
      checkPassword,
    });
console.log(data.message.length)
    if (data.status === "success") {
      setNotiStatus("success");
      // setTimeout(() => {
        navigate("/login");
      // }, 2000);
      return;
    }
    if (data.message[0] === "account") {
      setNotiStatus("account");
      setAccountInvalid(true);
      setEmailInvalid(false)
    }
    if (data.message[0] === "email") {
      setNotiStatus("email");
      setEmailInvalid(true);
      setAccountInvalid(false)
    }
    if (data.message.length > 1) {
      setNotiStatus("invalid");
      setAccountInvalid(true);
      setEmailInvalid(true);
    }
  };

  const handleClosePopup = () => {
    setNotiStatus("finished");
  };

  return (
    <>
      <div className={styles.notiContainer} onClick={handleClosePopup}>
        {notiStatus === "success" && (
          <TimePopup notification="success" title="" />
        )}
        {notiStatus === "account" && (
          <TimePopup notification="error" title="!" />
        )}
        {notiStatus === "email" && (
          <TimePopup notification="error" title="email!" />
        )}
        {notiStatus === "invalid" && (
          <TimePopup notification="error" title="Email" />
        )}
        {notiStatus === "incomplete" && (
          <TimePopup notification="error" title="" />
        )}
        {notiStatus === "code" && (
          <TimePopup notification="error" title="" />
        )}
        {notiStatus === "finish" && ""}
      </div>
      <div className={styles.container}>
        <div>
          <Logo className={styles.logo} />
        </div>
        <h1></h1>
        <div className={styles.inputGroup}>
          <div
            className={clsx(styles.inputContainer, {
              [styles.errorContainer]: accountInvalid,
            })}
          >
            <label className={styles.input}>
              
              <input
                type="text"
                value={account}
                placeholder=""
                maxLength="10"
                onChange={(e) => setAccount(e.target.value)}
              />
            </label>
            {accountInvalid ? (
              <p className={styles.errorMessage}></p>
            ) : (
              <></>
            )}
          </div>

          <div className={styles.inputContainer}>
            <label className={styles.input}>
              <div></div>
              <input
                type="text"
                value={name}
                placeholder=""
                maxLength="50"
                onChange={(e) => setName(e.target.value)}
              />
            </label>
          </div>

          <div
            className={clsx(styles.inputContainer, {
              [styles.errorContainer]: emailInvalid,
            })}
          >
            <label className={styles.input}>
              <div>Email</div>
              <input
                type="text"
                value={email}
                placeholder="Email"
                onChange={(e) => setEmail(e.target.value)}
              />
            </label>
            {emailInvalid ? (
              <p className={styles.errorMessage}>Email</p>
            ) : (
              <></>
            )}
          </div>

          <div className={styles.inputContainer}>
            <label className={styles.input}>
              <div></div>
              <input
                type="password"
                value={password}
                placeholder=""
                onChange={(e) => setPassword(e.target.value)}
              />
            </label>
          </div>

          <div className={styles.inputContainer}>
            <label className={styles.input}>
              <div></div>
              <input
                type="password"
                value={checkPassword}
                placeholder=""
                onChange={(e) => setCheckPassword(e.target.value)}
              />
            </label>
          </div>
        </div>
        <button className={styles.button} onClick={handleClick}>
          
        </button>
        <div>
          <Link className={styles.linkText} to="/login">
            <span></span>
          </Link>
        </div>
      </div>
    </>
  );
};

export default SignUpPage;
